import React, { useRef, useState, useLayoutEffect } from "react"
import * as am4core from "@amcharts/amcharts4/core"
import * as am4charts from "@amcharts/amcharts4/charts"
import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import Box from "@material-ui/core/Box"
import Typography from "@material-ui/core/Typography"
import Networking from "../../networking"
import { EN_factors, LL2NE } from "../../shared/utilities"
import { GNSS_FIX_TYPE } from "../../shared/constants"
import { useEffect } from 'react';

// Non-material-ui CSS, used to maintain the chart's aspect ratio
// This trick works because the percentage of paddingTop refers to the width of the parent element
const chartContainer = {
  // make sure the min function is the same for both width and paddingTop
  width: "calc(min(100%, 70vh))",
  paddingTop: "calc((min(100%, 70vh)) - 0px)",
  position: "relative",
} as React.CSSProperties

const chartStyle = {
  top: 0,
  left: 0,
  bottom: 0,
  right: 0,
  position: "absolute",
} as React.CSSProperties

class PathDisplay {
  latitudes: number[]
  longitudes: number[]
  easts: number[] // array of East values for robot
  norths: number[] // array of North values for robot
  vehicle_easts: number[] //array of East values for vehicle
  vehicle_norths: number[] //array of North values for vehicles
  E_factor: number
  N_factor: number
  lat_ref: number // latitude reference
  long_ref: number //longitude reference
  robot_east: number
  robot_north: number

  constructor() {
    this.latitudes = [0]
    this.longitudes = [0]
    this.easts = [0]
    this.norths = [0]
    this.vehicle_easts = [0]
    this.vehicle_norths = [0]
    this.E_factor = 0
    this.N_factor = 0
    this.lat_ref = 0
    this.long_ref = 0
    this.robot_east = 0
    this.robot_north = 0
  }

  // Robot Path 
  plotPathSeries(
    latitudes: number[],
    longitudes: number[],
    pathSeriesRef: React.MutableRefObject<any>,
    xAxisRef: React.MutableRefObject<any>,
    yAxisRef: React.MutableRefObject<any>,
    forceRun: boolean
  ) {
    if (forceRun === true || (this.latitudes !== latitudes && pathSeriesRef.current !== undefined)) {
      this.latitudes = latitudes
      this.longitudes = longitudes

      this.easts = []
      this.norths = []

      const [E_factor, N_factor] = EN_factors(latitudes[0])
      this.E_factor = E_factor
      this.N_factor = N_factor

      this.lat_ref = latitudes[0]
      this.long_ref = longitudes[0]

      for (let i = 0; i < latitudes.length; i++) {
        const [pos_north, pos_east] = LL2NE(
          latitudes[i],
          longitudes[i],
          this.lat_ref,
          this.long_ref,
          this.E_factor,
          this.N_factor
        )

        this.easts.push(pos_east)
        this.norths.push(pos_north)
      }

      const eastMin = Math.min(...this.easts)
      const eastMax = Math.max(...this.easts)
      const northMin = Math.min(...this.norths)
      const northMax = Math.max(...this.norths)

      const axisLength = Math.max(eastMax - eastMin, northMax - northMin)

      xAxisRef.current.min = eastMin - 3
      xAxisRef.current.max = eastMin + axisLength + 3
      yAxisRef.current.min = northMin - 3
      yAxisRef.current.max = northMin + axisLength + 3

      pathSeriesRef.current.data = this.createPathSeriesData()
    }
  }

  createPathSeriesData() {
    const pathSeriesData = []
    for (let i = 0; i < this.easts.length; i++) {
      pathSeriesData.push({ ax: this.easts[i], ay: this.norths[i], aValue: i })
    }
    return pathSeriesData
  }

  // Robot position
  plotRobotSeries(
    latitude: number,
    longitude: number,
    headingRadian: number,
    robotSeriesRef: React.MutableRefObject<any>
  ) {
    if (robotSeriesRef.current !== undefined) {
      const [pos_north, pos_east] = LL2NE(
        latitude,
        longitude,
        this.lat_ref,
        this.long_ref,
        this.E_factor,
        this.N_factor
      )

      robotSeriesRef.current.addData(
        { bx: pos_east, by: pos_north, angle: (headingRadian / Math.PI) * 180 },
        1
      )
      
      // For displaying the numerical values of robot location
      this.robot_east = pos_east
      this.robot_north = pos_north
    }
  }

  // Vehicle Path
  plotVehiclePathSeries(
    latitudes: number[],
    longitudes: number[],
    robot_lat: number[],
    robot_long: number[],
    vehiclePathSeriesRef: React.MutableRefObject<any>,
    xAxisRef: React.MutableRefObject<any>,
    yAxisRef: React.MutableRefObject<any>,
    forceRun: boolean
  ) {
    if (forceRun === true || (this.latitudes !== latitudes && vehiclePathSeriesRef.current !== undefined)) {
      this.latitudes = latitudes
      this.longitudes = longitudes

      this.vehicle_easts = []
      this.vehicle_norths = []

      const [E_factor, N_factor] = EN_factors(latitudes[0])
      this.E_factor = E_factor
      this.N_factor = N_factor

      this.lat_ref = robot_lat[0]
      this.long_ref = robot_long[0]

      for (let i = 0; i < latitudes.length; i++) {
        const [pos_north, pos_east] = LL2NE(
          latitudes[i],
          longitudes[i],
          this.lat_ref,
          this.long_ref,
          this.E_factor,
          this.N_factor
        )

        this.vehicle_easts.push(pos_east)
        this.vehicle_norths.push(pos_north)
      }

      const eastMin = Math.min(...this.vehicle_easts)
      const eastMax = Math.max(...this.vehicle_easts)
      const northMin = Math.min(...this.vehicle_norths)
      const northMax = Math.max(...this.vehicle_norths)

      const axisLength = Math.max(eastMax - eastMin, northMax - northMin)

      xAxisRef.current.min = eastMin - 3
      xAxisRef.current.max = eastMin + axisLength + 3
      yAxisRef.current.min = northMin - 3
      yAxisRef.current.max = northMin + axisLength + 3

      vehiclePathSeriesRef.current.data = this.createVehiclePathSeriesData()
    }
  }

  createVehiclePathSeriesData() {
    const vehiclePathSeriesData = []
    for (let i = 0; i < this.vehicle_easts.length; i++) {
      vehiclePathSeriesData.push({ cx: this.vehicle_easts[i], cy: this.vehicle_norths[i], cValue: i })
    }
    return vehiclePathSeriesData
  }

  // Vehicle Location
  plotVehiclePositionSeries(
    latitude: number,
    longitude: number,
    headingDeg: number,
    vehiclePositionSeriesRef: React.MutableRefObject<any>
  ) {
    if (vehiclePositionSeriesRef.current !== undefined) {
      const [pos_north, pos_east] = LL2NE(
        latitude,
        longitude,
        this.lat_ref,
        this.long_ref,
        this.E_factor,
        this.N_factor
      )

      vehiclePositionSeriesRef.current.addData(
        { dx: pos_east, dy: pos_north, veh_angle: headingDeg},
        1
      )
    }
  }
}

const pathDisplay = new PathDisplay()

const useStyles = makeStyles((theme) => ({
  notes: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(0.5),
    padding: theme.spacing(1),
    fontSize: 11,
  },
  messageBox: {
    maxHeight: 10,
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()
  const [isVehiclePathVisible, setIsVehiclePathVisible] = useState(false)

  const downloadTestData = () => {
    const link = document.createElement("a")
    link.href = `http://195.0.0.${networking.guiState.id}:3001/test-data.csv`
    link.click()
  }

  const pathSeriesRef: React.MutableRefObject<any> = useRef()
  const robotSeriesRef: React.MutableRefObject<any> = useRef()
  const vehiclePathSeriesRef: React.MutableRefObject<any> = useRef()
  const vehiclePositionSeriesRef: React.MutableRefObject<any> = useRef()
  const xAxisRef: React.MutableRefObject<any> = useRef()
  const yAxisRef: React.MutableRefObject<any> = useRef()
  const chartRef: React.MutableRefObject<any> = useRef();

  const toggleVehiclePathVisibility = () => {
    setIsVehiclePathVisible((prevState) => {
      const newVisibility = !prevState;
  
      // Toggle vehicle path visibility
      if (vehiclePathSeriesRef.current) {
        vehiclePathSeriesRef.current.visible = newVisibility;
      }
  
      // Get appropriate coordinates based on visibility
      const allEasts = newVisibility
        ? [...pathDisplay.easts, ...pathDisplay.vehicle_easts]
        : [...pathDisplay.easts];
  
      const allNorths = newVisibility
        ? [...pathDisplay.norths, ...pathDisplay.vehicle_norths]
        : [...pathDisplay.norths];
  
      // Calculate zoom bounds
      if (allEasts.length > 0 && allNorths.length > 0) {
        const eastMin = Math.min(...allEasts);
        const eastMax = Math.max(...allEasts);
        const northMin = Math.min(...allNorths);
        const northMax = Math.max(...allNorths);
  
        // Update axis ranges with a delay to ensure proper state update
        setTimeout(() => {
          if (xAxisRef.current && yAxisRef.current) {
            xAxisRef.current.min = eastMin - 3;
            xAxisRef.current.max = eastMax + 3;
            yAxisRef.current.min = northMin - 3;
            yAxisRef.current.max = northMax + 3;
  
            chartRef.current?.validateLayout();
          }
        }, 100);
      }
  
      return newVisibility;
    });
  };
  

  //Handle the initial zoom state of the chart (Default to view of robot path only):
  useEffect(() => {
    const allEasts = pathDisplay.easts; // Only robot path easts initially
    const allNorths = pathDisplay.norths; // Only robot path norths initially

    if (allEasts.length > 0 && allNorths.length > 0) {
      const eastMin = Math.min(...allEasts);
      const eastMax = Math.max(...allEasts);
      const northMin = Math.min(...allNorths);
      const northMax = Math.max(...allNorths);

      if (xAxisRef.current && yAxisRef.current) {
        setTimeout(() => {
          xAxisRef.current.min = eastMin - 3;
          xAxisRef.current.max = eastMax + 3;
          yAxisRef.current.min = northMin - 3;
          yAxisRef.current.max = northMax + 3;

          chartRef.current?.invalidateRawData();
          chartRef.current?.validateData();
          chartRef.current?.validateLayout();
        }, 100);
      }
    }
  }, []);

  pathDisplay.plotRobotSeries(
    networking.robotState.gps.latitude,
    networking.robotState.gps.longitude,
    networking.robotState.gps.heading,
    robotSeriesRef
  )

  pathDisplay.plotVehiclePositionSeries(
    networking.robotState.targetVehicle.latitude,
    networking.robotState.targetVehicle.longitude,
    networking.robotState.targetVehicle.heading,
    vehiclePositionSeriesRef
  )  

  useLayoutEffect(() => {
    let chart = am4core.create("chartdiv", am4charts.XYChart)
    chartRef.current = chart

    chart.paddingRight = 20

    // Create X axis
    let xAxis = chart.xAxes.push(new am4charts.ValueAxis())
    xAxis.renderer.minGridDistance = 40
    xAxis.title.text = "East"
    xAxis.renderer.baseGrid.strokeOpacity = 0.5
    xAxis.renderer.baseGrid.strokeWidth = 1
    xAxis.strictMinMax = true

    // Create y axis
    let yAxis = chart.yAxes.push(new am4charts.ValueAxis())
    yAxis.renderer.minGridDistance = 40
    yAxis.title.text = "North"
    yAxis.renderer.baseGrid.strokeOpacity = 0.5
    yAxis.renderer.baseGrid.strokeWidth = 1
    yAxis.strictMinMax = true

    // Robot Path series
    let pathSeries = chart.series.push(new am4charts.LineSeries())
    pathSeries.dataFields.valueX = "ax"
    pathSeries.dataFields.valueY = "ay"
    pathSeries.dataFields.value = "aValue"
    pathSeries.strokeWidth = 2

    let bullet1 = pathSeries.bullets.push(new am4charts.CircleBullet())
    bullet1.circle.radius = 3

    // Robot Location series
    let robotSeries = chart.series.push(new am4charts.LineSeries())
    robotSeries.dataFields.valueX = "bx"
    robotSeries.dataFields.valueY = "by"
    robotSeries.strokeWidth = 2
    robotSeries.stroke = am4core.color("red")

    robotSeries.data = [ // It must be initialized with two data points to prevent blinking
      { bx: 0, by: 0, angle: 0 },
      { bx: 0, by: 0, angle: 0 },
    ]

    let bullet2 = robotSeries.bullets.push(new am4charts.Bullet())
    bullet2.propertyFields.rotation = "angle"

    let arrow = bullet2.createChild(am4core.Triangle) // Add a triangle to act as an arrow for robot position
    arrow.horizontalCenter = "middle"
    arrow.verticalCenter = "middle"
    arrow.direction = "top"
    arrow.width = 10
    arrow.height = 25
    arrow.fill = am4core.color("#ffffff00")
    arrow.strokeWidth = 3

    chart.numberFormatter.numberFormat = "#.##"
    bullet1.tooltipText = "[bold]Index {value}[/] ({valueX}, {valueY})"

    // Vehicle Path Series
    let vehiclePathSeries = chart.series.push(new am4charts.LineSeries())
    vehiclePathSeriesRef.current = vehiclePathSeries;
    vehiclePathSeries.dataFields.valueX = "cx"
    vehiclePathSeries.dataFields.valueY = "cy"
    vehiclePathSeries.dataFields.value = "cValue"
    vehiclePathSeries.strokeWidth = 2
    vehiclePathSeries.fill = am4core.color("orange")
    vehiclePathSeries.stroke = am4core.color("orange")
    let bullet3 = vehiclePathSeries.bullets.push(new am4charts.CircleBullet())
    bullet3.circle.radius = 3
    vehiclePathSeries.hidden = !isVehiclePathVisible // Hide or show vehicle path based on state

    // Vehicle Location Series
    let vehiclePositionSeries = chart.series.push(new am4charts.LineSeries())
    vehiclePositionSeries.dataFields.valueX = "dx"
    vehiclePositionSeries.dataFields.valueY = "dy"
    vehiclePositionSeries.strokeWidth = 2
    vehiclePositionSeries.stroke = am4core.color("purple")
    
    vehiclePositionSeries.data = [ // It must be initialized with two data points to prevent blinking
      { dx: 0, dy: 0, veh_angle: 0 },
      { dx: 0, dy: 0, veh_angle: 0 },
    ]

    let bullet4 = vehiclePositionSeries.bullets.push(new am4charts.Bullet())
    bullet4.propertyFields.rotation = "veh_angle"

    let vehicleArrow = bullet4.createChild(am4core.Triangle) //Triangle for Vehicle Position
    vehicleArrow.horizontalCenter = "middle"
    vehicleArrow.verticalCenter = "middle"
    vehicleArrow.direction = "top"
    vehicleArrow.width = 10
    vehicleArrow.height = 25
    vehicleArrow.fill = am4core.color("#ffffff00")
    vehicleArrow.strokeWidth = 3

    chart.numberFormatter.numberFormat = "#.##"
    bullet3.tooltipText = "[bold]Index {value}[/] ({valueX}, {valueY})"
    vehicleArrow.visible = isVehiclePathVisible; // Hide or show vehicle location based on state

    //Get current ref values
    pathSeriesRef.current = pathSeries
    robotSeriesRef.current = robotSeries
    vehiclePathSeriesRef.current = vehiclePathSeries
    vehiclePositionSeriesRef.current = vehiclePositionSeries
    xAxisRef.current = xAxis
    yAxisRef.current = yAxis

    pathDisplay.plotPathSeries(
      networking.pathToFollow.latitudes,
      networking.pathToFollow.longitudes,
      pathSeriesRef,
      xAxisRef,
      yAxisRef,
      true
    )

    pathDisplay.plotVehiclePathSeries(
      networking.vehiclePathToFollow.latitudes,
      networking.vehiclePathToFollow.longitudes,
      networking.pathToFollow.latitudes,
      networking.pathToFollow.longitudes,
      vehiclePathSeriesRef,
      xAxisRef,
      yAxisRef,
      true
    )

    return () => {
      chart.dispose()
    }
  }, [networking, isVehiclePathVisible])

  return (
    <div>
      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          networking.senders.startPathFollowing()
        }}
      >
        start test
      </Button>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          networking.senders.returnToStart()
        }}
      >
        return to start position
      </Button>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          downloadTestData()
        }}
      >
        download test data
      </Button>

      <Typography variant="body2" className={classes.body}>
        GPS status: {GNSS_FIX_TYPE[networking.robotState.gps.status]}
        <br />
        Robot Location: ({pathDisplay.robot_east.toFixed(2)}, {pathDisplay.robot_north.toFixed(2)})
        <br />
        Heading: {((networking.robotState.gps.heading / Math.PI) * 180).toFixed(0)} deg
        <br />
        Speed:{" "}
        {Math.sqrt(
          networking.robotState.gps.eastVelocity ** 2 + networking.robotState.gps.northVelocity ** 2
        ).toFixed(2)}{" "}
        m/s
      </Typography>

      {/* Checkbox for toggling vehicle path visibility */}
      <label>
        <input
          type="checkbox"
          checked={isVehiclePathVisible}
          onChange={toggleVehiclePathVisibility}
        />
        Show Vehicle
      </label>

      <div style={chartContainer}>
        <div id="chartdiv" style={chartStyle} />
      </div>

      <Typography variant="h6" style={{ marginTop: "15px" }}>
        Test Log
      </Typography>
      <Box
        mb={2}
        display="flex"
        flexDirection="column"
        height="250px"
        style={{
          border: "2px solid lightgrey",
          overflow: "hidden",
          overflowY: "scroll",
          overflowX: "scroll",
          padding: "10px",
        }}
      >
        {networking.testLogMessages.map((msg) => (
          <div key={msg}> {msg} </div>
        ))}
      </Box>
    </div>
  )
}
