import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import { RobotState } from "../shared/interfaces"
import { GNSS_FIX_TYPE } from "../shared/constants"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  expand: {
    margin: "0 auto",
    paddingTop: 0,
    paddingBottom: 0,
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  cardContent: {
    paddingBottom: 0,
  },
}))

export default function Component({ robotState }: { robotState: RobotState }) {
  const classes = useStyles()
  if(robotState.gps.diff_age > 60000) {
    return (
      <Card variant="outlined">
        <CardContent className={classes.cardContent}>
          <Typography variant="h5" className={classes.title}>
            GPS
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Status: {GNSS_FIX_TYPE[robotState.gps.status]}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Latitdue (deg): {robotState.gps.latitude.toFixed(7)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Longitude (deg): {robotState.gps.longitude.toFixed(7)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Heading (deg): {((robotState.gps.heading / Math.PI) * 180).toFixed(2)}
          </Typography>
          
          <Typography variant="body1" className={classes.body}>
            Longitudinal Velocity (m/s): {""}
            {((robotState.gps.northVelocity * Math.cos(robotState.gps.heading * Math.PI/180)) + (robotState.gps.eastVelocity * Math.sin(robotState.gps.heading * Math.PI/180))).toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Number of Satellites: {robotState.gps.num_satellites}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Differential Age (ms): N/A
          </Typography>

        </CardContent>
      </Card>
    )
}
  else {
    return (
      <Card variant="outlined">
        <CardContent className={classes.cardContent}>
          <Typography variant="h5" className={classes.title}>
            GPS
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Status: {GNSS_FIX_TYPE[robotState.gps.status]}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Latitdue (deg): {robotState.gps.latitude.toFixed(7)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Longitude (deg): {robotState.gps.longitude.toFixed(7)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Heading (deg): {((robotState.gps.heading / Math.PI) * 180).toFixed(2)}
          </Typography>
          
          <Typography variant="body1" className={classes.body}>
            Longitudinal Velocity (m/s): {""}
            {((robotState.gps.northVelocity * Math.cos(robotState.gps.heading * Math.PI/180)) + (robotState.gps.eastVelocity * Math.sin(robotState.gps.heading * Math.PI/180))).toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Number of Satellites: {robotState.gps.num_satellites}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Differential Age (ms): {robotState.gps.diff_age}
          </Typography>

        </CardContent>
      </Card>
    )
  }
}
