import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import { RobotState } from "../shared/interfaces"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  expand: {
    margin: "0 auto",
    paddingTop: 0,
    paddingBottom: 0,
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  cardContent: {
    paddingBottom: 0,
  },
}))

export default function Component({ robotState }: { robotState: RobotState }) {
  const classes = useStyles()
  
  if (robotState.mechanicalBrake.hasBrake) {
    return (
      <Card variant="outlined">
        <CardContent className={classes.cardContent}>
          <Typography variant="h5" className={classes.title}>
            Mechanical Brake
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Has Brake: {robotState.mechanicalBrake.hasBrake.toString()}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Brake Status: {robotState.mechanicalBrake.brakeStatus}
          </Typography>

        </CardContent>
      </Card>
    )
  }
  else {
    return (
      <Card variant="outlined">
        <CardContent className={classes.cardContent}>
          <Typography variant="h5" className={classes.title}>
            Mechanical Brake
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Has Brake: {robotState.mechanicalBrake.hasBrake.toString()}
          </Typography>
          
        </CardContent>
      </Card>
    )
  }

}
