import React, { useState, useEffect } from "react"

import { makeStyles } from "@material-ui/core/styles"
import Modal from "@material-ui/core/Modal"

import Networking from "../networking"

function getModalStyle() {
  const top = 50
  const left = 50

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  }
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(2, 4, 3),
  },
}))

export default function Component({ networking }: { networking: Networking }) {
  const classes = useStyles()
  const [modalStyle] = React.useState(getModalStyle)
  const [open, setOpen] = useState(false)

  // function reset() {
  //   networking.senders.resetMicroController()
  // }

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h3>The micro-controller requires a reset. Please move robot to flat ground and press the reset button.</h3>
    </div>
  )

  useEffect(() => {
    setOpen(!networking.robotState.portentaHeartbeat)
  },[networking.robotState.portentaHeartbeat])

  return (
    <div>
      <Modal open={open}>{body}</Modal>
    </div>
  )
}
