import React, { useRef, useLayoutEffect } from "react"
import * as am4core from "@amcharts/amcharts4/core"
import * as am4charts from "@amcharts/amcharts4/charts"
import { EN_factors, LL2NE} from "../../shared/utilities"
import PathRecorder from "../../path-recorder"

import Button from "@material-ui/core/Button"
import { makeStyles } from "@material-ui/core/styles"

// Non-material-ui CSS, used to maintain the chart's aspect ratio
// This trick works because the percentage of paddingTop refers to the width of the parent element
const chartContainer = {
  // make sure the min function is the same for both width and paddingTop
  width: "calc(min(100%, 90vh))",
  paddingTop: "calc((min(100%, 90vh)) - 15px)",
  position: "relative",
} as React.CSSProperties

const chartStyle = {
  top: 0,
  left: 0,
  bottom: 0,
  right: 0,
  position: "absolute",
} as React.CSSProperties

class PathViewer {
  latitudes: number[]
  longitudes: number[]
  easts: number[] // array of East values
  norths: number[] // array of North values
  E_factor: number
  N_factor: number
  lat_ref: number // latitude reference
  long_ref: number //longitude reference

  constructor() {
    this.latitudes = [0]
    this.longitudes = [0]
    this.easts = [0]
    this.norths = [0]
    this.E_factor = 0
    this.N_factor = 0
    this.lat_ref = 0
    this.long_ref = 0
  }

  importFromRecorder(latitudes: number[], longitudes: number[], chartRef: React.MutableRefObject<any>) {
    this.latitudes = latitudes
    this.longitudes = longitudes

    this.easts = []
    this.norths = []

    const [E_factor, N_factor] = EN_factors(latitudes[0])
    this.E_factor = E_factor
    this.N_factor = N_factor

    this.lat_ref = latitudes[0]
    this.long_ref = longitudes[0]

    for (let i = 0; i < latitudes.length; i++) {
      const [pos_north, pos_east] = LL2NE(
        latitudes[i],
        longitudes[i],
        this.lat_ref,
        this.long_ref,
        this.E_factor,
        this.N_factor
      )

      this.easts.push(pos_east)
      this.norths.push(pos_north)
    }

    chartRef.current.data = this.createChartData()
  }

  isNumeric(str: string) {
    return (
      !isNaN(Number(str)) && // use type coercion to parse the _entirety_ of the string (`parseFloat` alone does not do this)...
      !isNaN(parseFloat(str))
    ) // ...and ensure strings of whitespace fail
  }

  importFromFile(importFileRef: React.MutableRefObject<any>, chartRef: React.MutableRefObject<any>) {
    this.easts = []
    this.norths = []
    this.latitudes = []
    this.longitudes = []

    const file = importFileRef.current.files[0]
    const reader = new FileReader()
    reader.addEventListener("load", (event: any) => {
      const text = event.target.result
      const splitText = text.split("\n")

      let lineNumber = 1
      for (const line of splitText) {
        // omit the first line in file
        if (lineNumber === 1) {
          lineNumber++
          continue
        }

        const latLong = line.split(/ |\t/)
        if (latLong.length === 2) {
          if (this.isNumeric(latLong[0]) && this.isNumeric(latLong[1])) {
            this.latitudes.push(Number(latLong[0]))
            this.longitudes.push(Number(latLong[1]))
          }
        }
      }

      const [E_factor, N_factor] = EN_factors(this.latitudes[0])
      this.E_factor = E_factor
      this.N_factor = N_factor

      this.lat_ref = this.latitudes[0]
      this.long_ref = this.longitudes[0]

      for (let i = 0; i < this.latitudes.length; i++) {
        const [pos_north, pos_east] = LL2NE(
          this.latitudes[i],
          this.longitudes[i],
          this.lat_ref,
          this.long_ref,
          this.E_factor,
          this.N_factor
        )

        this.easts.push(pos_east)
        this.norths.push(pos_north)
      }

      importFileRef.current.value = "" // this is needed for importing the same file that has been saved over

      chartRef.current.data = this.createChartData()
    })
    reader.readAsText(file)
  }

  createChartData() {
    const chartData = []
    for (let i = 0; i < this.easts.length; i++) {
      chartData.push({ ax: this.easts[i], ay: this.norths[i], aValue: i })
    }
    return chartData
  }

  twoPointDistance(x1: number, y1: number, x2: number, y2: number) {
    return Math.sqrt((x2 - x1) ** 2 + (y2 - y1) ** 2)
  }
}

interface Props {
  pathRecorder: PathRecorder
}

const pathViewer = new PathViewer()

const useStyles = makeStyles((theme) => ({
  body: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
}))

export default function Component({ pathRecorder }: Props) {
  const classes = useStyles()
  const importFileRef: React.MutableRefObject<any> = useRef()

  // XY Chart
  const chartRef: React.MutableRefObject<any> = useRef()
  useLayoutEffect(() => {
    let chart = am4core.create("chartdiv", am4charts.XYChart)

    chart.paddingRight = 20

    chart.data = pathViewer.createChartData()

    // Create X axis
    let xAxis = chart.xAxes.push(new am4charts.ValueAxis())
    xAxis.renderer.minGridDistance = 40
    xAxis.title.text = "East"
    xAxis.renderer.baseGrid.strokeOpacity = 0.3
    xAxis.renderer.baseGrid.strokeWidth = 1

    // Create y axis
    let yAxis = chart.yAxes.push(new am4charts.ValueAxis())
    yAxis.title.text = "North"
    yAxis.renderer.baseGrid.strokeOpacity = 0.3
    yAxis.renderer.baseGrid.strokeWidth = 1

    // Create series
    let series1 = chart.series.push(new am4charts.LineSeries())
    series1.dataFields.valueX = "ax"
    series1.dataFields.valueY = "ay"
    series1.dataFields.value = "aValue"
    series1.strokeWidth = 2

    let bullet1 = series1.bullets.push(new am4charts.CircleBullet())
    bullet1.circle.radius = 3

    chart.numberFormatter.numberFormat = "#.##"
    bullet1.tooltipText = "[bold]Index {value}[/] ({valueX}, {valueY})"

    //scrollbars
    chart.scrollbarX = new am4core.Scrollbar()
    chart.scrollbarY = new am4core.Scrollbar()

    chartRef.current = chart

    return () => {
      chart.dispose()
    }
  }, [])

  return (
    <div>
      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          pathViewer.importFromRecorder(pathRecorder.latitudes, pathRecorder.longitudes, chartRef)
        }}
      >
        import from recorder
      </Button>

      <Button className={classes.button} variant="outlined" color="primary" component="label">
        import from file
        <input
          type="file"
          ref={importFileRef}
          onChange={() => pathViewer.importFromFile(importFileRef, chartRef)}
          hidden
        />
      </Button>

      <div style={chartContainer}>
        <div id="chartdiv" style={chartStyle} />
      </div>
    </div>
  )
}
